export class Attribute {
    attrs;
    constructor(initial = {}) {
        this.attrs = initial;
    }
    static create(initial = {}) {
        return new Attribute(initial);
    }
    set(attrs) {
        this.attrs = {
            ...this.attrs,
            ...attrs,
        };
    }
    get() {
        return this.attrs;
    }
}
